
'use server';

import { getFirestore, serverTimestamp } from 'firebase-admin/firestore';
import * as admin from 'firebase-admin';
import type { ProfileData } from '@/types';
import { revalidatePath } from 'next/cache';

export async function updateUserProfile(formData: FormData) {
  try {
    if (admin.apps.length === 0) {
        const serviceAccount = JSON.parse(
            process.env.FIREBASE_SERVICE_ACCOUNT_KEY as string
        );
        admin.initializeApp({
            credential: admin.credential.cert(serviceAccount),
        });
    }
  } catch (e: any) {
    console.error('Firebase Admin SDK initialization error:', e.message);
    return { success: false, message: 'Server configuration error.' };
  }

  const db = getFirestore();
  const uid = formData.get('uid') as string;

  if (!uid) {
    return { success: false, message: 'User not authenticated.' };
  }

  const userDocRef = db.collection('users').doc(uid);

  const data: Partial<ProfileData> = {
    firstName: formData.get('firstName') as string,
    middleName: formData.get('middleName') as string,
    surname: formData.get('surname') as string,
    dob: new Date(formData.get('dob') as string).toISOString(),
    country: formData.get('country') as string,
    addressLine1: formData.get('addressLine1') as string,
    city: formData.get('city') as string,
    taluka: formData.get('taluka') as string,
    district: formData.get('district') as string,
    state: formData.get('state') as string,
    pincode: formData.get('pincode') as string,
    schoolName: formData.get('schoolName') as string,
    grade: formData.get('grade') as string,
    mobileNo: formData.get('mobileNo') as string,
    whatsappNo: formData.get('whatsappNo') as string,
    teacherId: formData.get('teacherId') as string,
    instituteName: formData.get('instituteName') as string,
    instituteCountry: formData.get('instituteCountry') as string,
    instituteAddressLine1: formData.get('instituteAddressLine1') as string,
    instituteCity: formData.get('instituteCity') as string,
    instituteTaluka: formData.get('instituteTaluka') as string,
    instituteDistrict: formData.get('instituteDistrict') as string,
    instituteState: formData.get('instituteState') as string,
    institutePincode: formData.get('institutePincode') as string,
  };

  const sanitizedData: Partial<ProfileData> & { updatedAt: any } = {
    ...data,
    updatedAt: serverTimestamp(),
  };
  
  delete (sanitizedData as any).role;
  delete (sanitizedData as any).status;
  delete (sanitizedData as any).subscriptionStatus;
  delete (sanitizedData as any).isAdmin; 
  delete (sanitizedData as any).createdAt;

  try {
    await userDocRef.update(sanitizedData);
    revalidatePath('/profile');
    revalidatePath('/'); // Revalidate home in case header data changed
    return { success: true, message: 'Profile updated successfully.' };
  } catch (error: any) {
    console.error('Error updating profile:', error);
    return { success: false, message: 'Could not update profile.' };
  }
}
